import React, { useMemo } from 'react';
import './AnimalComponent.css';
import "animate.css/source/_base.css";

import perro from '../assets/audio/perro.mp3';
import gato from '../assets/audio/cat.mp3';
import leon from '../assets/audio/lion.mp3';
import cerdo from '../assets/audio/pig.mp3';
import oso from '../assets/audio/bear.mp3';

export const AnimalComponent = () => {

    let audios = [];
    audios.push(perro);
    audios.push(gato);
    audios.push(leon);
    audios.push(cerdo);
    audios.push(oso);

    let audiosObject = [];
    for (let i = 0; i < audios.length; i++) {
        audiosObject.push(new Audio(audios[i]));
    }

    const inputRefs = useMemo(() => Array(audios.length).fill(null).map(i => React.createRef()), []);

    const playAudio = (j) => {

        for (let i = 0; i < audiosObject.length; i++) {
            audiosObject[i].currentTime = 0;
            audiosObject[i].pause();
            if (inputRefs[i].current) {
                inputRefs[i].current.classList.remove("playing");
                inputRefs[i].current.parentElement.classList.remove("loading");
            }
        }

        audiosObject[j].play();

        inputRefs[j].current.classList.add("playing");
        inputRefs[j].current.parentElement.classList.remove("loading");

        setTimeout(() => {
            inputRefs[j].current.parentElement.classList.add("loading");
            inputRefs[j].current.classList.remove("playing");

            audiosObject[j].onended = function () {
                inputRefs[j].current.parentElement.classList.remove("loading");
            }

        }, 200);

    }

    return (
        <div className="container text-center mt-5 principal_div">
            <h1 className="animate__animated animate__zoomIn">Animales</h1>
            <div className="row justify-content-center mt-3 mb-2 pb-4">
                <div className="btn col-6 text-center p-0 emoji">
                    <span ref={inputRefs[0]} onClick={() => playAudio(0)}>üê∂</span>
                </div>
                <div className="btn col-6 text-center p-0 emoji">
                    <span ref={inputRefs[1]} onClick={() => playAudio(1)}>üê±</span>
                </div>
                <div className="btn col-6 text-center p-4 emoji">
                    <span ref={inputRefs[2]} onClick={() => playAudio(2)}>ü¶Å</span>
                </div>
                <div className="btn col-6 text-center p-4 emoji">
                    <span ref={inputRefs[3]} onClick={() => playAudio(3)}>üê∑</span>
                </div>
                <div className="btn col-6 text-center p-4 emoji">
                    <span ref={inputRefs[4]} onClick={() => playAudio(4)}>üêª</span>
                </div>
            </div>
        </div>
    )
}
